import { TitleCasePipe } from '@angular/common';
import { Component, Inject, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialog, MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { Observable, of } from 'rxjs';
import { debounceTime, filter, map, startWith, switchMap } from 'rxjs/operators';
import { CommonService } from 'src/app/core/services/common.service';
import { PartyService } from 'src/app/core/services/party.service';
import { SaleInvoiceService } from 'src/app/core/services/sale-invoice.service';
import { SaleOrderService } from 'src/app/core/services/sale-order.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';
import { WarehouseService } from 'src/app/core/services/warehouse.service';
import lang from 'src/assets/langTranslation/language_translation';
import { from } from 'rxjs';
import { ContactlistModuleRoutingModule } from 'src/app/pages/contactlist-module/contactlist-module-routing.module';
import * as moment from 'moment';

@Component({
  selector: 'app-add-package',
  templateUrl: './add-package.component.html',
  styleUrls: ['./add-package.component.scss']
})
export class AddPackageComponent implements OnInit {

  public lang = lang.UAE;
  public maxDate: Date;
  public currentUser = JSON.parse(localStorage.getItem("currentUser"))
  public packageForm: any;
  public submitted: boolean = false;
  public today: Date;
  countryList: any[];
  invoiceId: any;
  sales_products: any[];
  selectedProducts: any;
  partyId: any;
  invoiceNo: any;
  lastInvoiceNumber: any;
  isPacked: any;
  customerNames: Observable<any[]>;
  saleOrderOptions: Observable<any[]> = of([]);
  filteredCustomerNames: Observable<any[]>;  
  public allPartyList: any = [];
  searchedProduct: any;
  selectAll: boolean = false;
  isSaleInvocie: boolean = false;
  packageData: any;

  constructor(private formBuilder: FormBuilder,
    public router: Router, private commonService: CommonService,
    private toastService: ToastNotificationService, public dialog: MatDialog,
    private titleCasePipe: TitleCasePipe,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogRef: MatDialogRef<AddPackageComponent>,
    private warehouseService: WarehouseService,
    private saleOrderService: SaleOrderService,
    private partyService: PartyService,
    private saleInvoiceService: SaleInvoiceService
  ) {
    this.today = new Date();
    this.packageData = this.data.packageData
    this.invoiceId = this.data.invoiceId;
  }

  async ngOnInit(): Promise<void> {
    try {
      this.packageForm = this.formBuilder.group({
        name: [null],
        saleInvoice: [null],
        customerName: [{value: null, disabled: true}],
        billingAddress: [{value: null, disabled: true}],
        shippingAddress: [{value: null, disabled: true}],
        ewayBillNo: [{value: null, disabled: true}],
        mobile: [{value: null, disabled: true}],
        invoiceNo: [{value: null, disabled: true}],
        invoiceDate: [{value: null, disabled: true}],
        totalBillAmount: [{value: null, disabled: true}],
        billGstAmount: [null],
        warehouseName: [{value: null, disabled: true}],
        subcheckbox: this.formBuilder.array([]),
      });


      if(this.packageData) {
        this.isPacked = true;
        this.packageForm.patchValue({
          name: this.packageData?.customerName,
          saleInvoice: this.packageData?.invoiceNo
        })

        await this.getReceiptById(this.invoiceId)
      }

      this.customerNames = await this.packageForm.controls.name.valueChanges.pipe(
        startWith(''),
        debounceTime(300),
        switchMap(name => this.getContactName(name))
      );
      
      this.saleOrderOptions = this.packageForm.controls.saleInvoice.valueChanges.pipe(
        startWith(''),
        debounceTime(300),
        switchMap(saleInvoice => {
          const selectedCustomerName = this.packageForm.value.name;
          if (selectedCustomerName) {
            return this.getSaleOrderOptions(saleInvoice, selectedCustomerName);
          } else {
            return of([]); // Empty array when no customer name is selected
          }
        })
      );

      this.packageForm.controls.saleInvoice.valueChanges.pipe(
        debounceTime(300),
        filter(selectedSaleInvoice => !!selectedSaleInvoice) // Only proceed if saleInvoice has a value
      ).subscribe(async (selectedSaleInvoice) => {
        const selectedCustomerName = this.packageForm.value.name;
        let receiptId;

        await new Promise<void>((resolve) => {
          this.saleOrderOptions.subscribe(items => {
            const selectedInvoice = items.find(invoice => invoice.invoiceNo === selectedSaleInvoice);
            receiptId = selectedInvoice ? selectedInvoice.id : null;
            resolve();
          });
        });

        if (selectedCustomerName) {
          await this.getReceiptById(receiptId);
        }
      });

      this.getSaleInvoiceNumber()
    } catch (error) {
        this.toastService.toastMsg({
          title: 'Error',
          content: error
        })
    }
  }  
  
  receiptId(receiptId: any) {
    throw new Error('Method not implemented.');
  }
  
  getContactName(name): Observable<any[]> {
    return from(this.partyService.getAllPartiesDetailsSearch({}, this.currentUser.parentId ? this.currentUser.parentId : this.currentUser.id, 500, 1, name.toLowerCase()))
    .pipe(
        map((res: any) => {
          if (res.success) {
            return res.data.pageData.map(item => item.partyName);
          } else {
            return [];
          }
        })
      );
  }
  
  get formArr() {
    return this.packageForm.get("subcheckbox") as FormArray;
  }

  addDetailsButtonClick(i): void {
    const content = this.packageForm.get("subcheckbox") as FormArray;
    content.push(this.addRolesPermission(i));
  }

  addRolesPermission(id): FormGroup {
    return new FormGroup({
      read: new FormControl(false),
    });
  }
  
  get f() {
    return this.packageForm.controls;
  }

  cancel() {
    this.dialogRef.close();
  }

  isAllProductsPacked(){
    return this.sales_products.every(item => item.isPacked)
  }

  toggleSelectAll() {
    // this.selectAll = !this.selectAll;
    this.sales_products.forEach(item => item.checked = this.selectAll);
  }
  
  updateSelectAllState() {
    this.selectAll = this.sales_products.every(item => item.checked);
  }
  
  getSaleOrderOptions(saleInvoice, name): Observable<any[]> {
    // Implement the logic to fetch sale order options based on the selected customer and sale invoice
      try {
        return from(this.saleOrderService.getSaleOrderList({}, this.currentUser.id, 'SALEORDER', 500, 1, undefined, name))
          .pipe(
            map((res: any) => {
              return res.data.pageData.map(item => ({ invoiceNo: item.invoiceNo, id: item.id }));
            },
              (err) => {
                if (err.error.expose) {
                  this.toastService.toastMsg({
                    title: "Error",
                    content: this.titleCasePipe.transform(err.error.error_message),
                  });
                  return of([]); // Return an empty array in case of an error
                }
                else {
                  this.toastService.toastMsg({
                    title: "Error",
                    content: "Something Went Wrong.",
                  });
                  return of([]); // Return an empty array in case of an error
                }
              })
          )
      } catch (error) {
        console.error('Error fetching sale order list:', error);
        return of([]); // Return an empty array in case of an exception
      }
  }

  numberOnly(event): boolean {
    const charCode = (event.which) ? event.which : event.keyCode;
    if (charCode > 31 && (charCode < 48 || charCode > 57)) {
      return false;
    }
    return true;
  }

  numberDecimalOnly(event): boolean {
    var charCode = (event.which) ? event.which : event.keyCode;
    if (charCode != 46 && charCode > 31
      && (charCode < 48 || charCode > 57)) {
      return false;
    }
    return true;
  }

  getReceiptById(receiptId) {
    this.saleInvoiceService.getSaleInvoiceById({}, receiptId).then(async (res) => {
      if(res.success) {
        this.isSaleInvocie = true;
        this.sales_products = await res.data?.sales_products?.map(product => ({
          ...product,
          checked: product.isPacked === true // Set checked based on isPacked value
        })) || [];        
        this.updateSelectAllState()
        this.partyId = res.data.partyId;
        this.packageForm.patchValue({
          customerName : res.data?.customerName,
          billingAddress: res.data?.billingAddress,
          shippingAddress: res.data?.shippingAddress,
          ewayBillNo: res.data?.ewayBillNo,
          mobile: res.data?.mobile,
          invoiceNo: res.data?.invoiceNo,
          invoiceDate: res.data?.invoiceDate ? (moment(res.data?.invoiceDate).format("DD-MMM-YYYY")) : (moment(this.maxDate).format("DD-MMM-YYYY")),
          totalBillAmount: res.data?.totalBillAmount,
          billGstAmount: res.data?.billGstAmount,
          warehouseName: res.data?.warehouse?.name,
        })
        // this.isPacked = res.data.sales_products?.isPacked
      }
    })
  }
  
  getSaleInvoiceNumber() {
    this.saleInvoiceService
      .getSaleInvoiceNumber({}, this.currentUser.id)
      .then((res) => {
          this.invoiceNo = String(res.data.prefix).concat(String(res.data.suffix)),
          this.lastInvoiceNumber = res.data.suffix;
      });
  }

  saveSelectedProducts() {
    // Filter the selected products and Filter out the already packed products
    this.selectedProducts = this.sales_products.filter(product => !product.isPacked && product.checked);
    // Check if any product is selected
    if (this.selectedProducts.length === 0) {
      // Handle the case when no product is selected
      this.toastService.toastMsg({
        title: 'Error',
        content: 'No products selected.'
      })
    } else {
      // Prepare the payload for the API call
      const payload = {
          invoiceDetails: {
              "saleOrderId": this.invoiceId,
              "cashSale": false,
              "userId": this.currentUser.id,
              "partyId": this.partyId,
              "invoiceNo": this.invoiceNo,
              "lastInvoiceNumber": this.lastInvoiceNumber,
              "partialPack": this.selectAll ? false : true,
              "ewayBillNo": "1212"
          },
        products: this.selectedProducts.map(product => ({
          quantity: product.quantity,
          barcode: product.barcode,
          saleProductId: product.id,
        })),
      };
      this.warehouseService.packProducts(payload).then(
        (res) => {
          if(res.success) {
            this.dialogRef.close();
            this.toastService.toastMsg({
              title: 'Success',
              content: 'Products sent to packaging successfully'
            })
            this.commonService.notifyDataAdded()
          }
        },
        (error) => {
          this.toastService.toastMsg({
            title: 'Error',
            content: 'Error sending products to packaging:' + `${error}`
          })
        }
      );
    }
  }
  
  onSave() {
    this.submitted = true;
    if (this.packageForm.invalid) {
      this.toastService.toastMsg({
        title: "Error",
        content: "Fill All Required Fields.",
      });
      return false;
    } else {
      const data = {
          // "id": "4", //optional
          "userId": this.currentUser.id,
          "name": this.packageForm.value.name,
          "saleInvoice": this.packageForm.value.saleInvoice,
      }
      this.warehouseService.postWarehouse(data).then( res => {
        if(res.success) {
          this.dialogRef.close();
          this.commonService.notifyDataAdded()
          this.toastService.toastMsg({
            title: "Success",
            content: "Shipping Data Addedd Successfully!!!",
          });
        }
      })
    }
  }

  // getAllCountries() {
  //   this.partyService.getAllCountries({}).then((res) => {
  //     this.countryList = res.data;
  //   });
  // }
}
